// ,fu
const getTreeData = (data) => {
  // json  
  for (var i = 0; i < data.length; i++) {
    if (data[i].children.length < 1) {
      // childrenchildrenundefined
      data[i].children = undefined
    } else {
      // children  
      getTreeData(data[i].children)
    }
  }
  return data
}
// 
const lastArr = (data) => {
  return data.filter((item, index, arr) => {
    return index !== arr.length - 1
  })
}
// 
const addComma = (num) => {
  var parts = num.toString().split('.')
  parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',')
  return parts.join('.')
}
// 
const nowTime = () => {
  var myDate = new Date()
  myDate.getYear() // (2)
  let nowYear = myDate.getFullYear() // (4,1970-????)
  let nowMonth = myDate.getMonth() + 1 // (0-11,01)
  let nowDate = myDate.getDate() // (1-31)

  let nowDay = myDate.getDay() + 1 // X(0-6,0)
  myDate.getTime() // (1970.1.1)
  let nowHours = myDate.getHours()
  let nowMinutess = myDate.getMinutes()
  myDate.getHours() // (0-23)
  myDate.getMinutes() // (0-59)
  myDate.getSeconds() // (0-59)
  myDate.getMilliseconds() // (0-999)
  myDate.toLocaleDateString() // 
  var mytime = myDate.toLocaleTimeString() // 
  myDate.toLocaleString() // 
  // eslint-disable-next-line no-unused-expressions
  nowMonth = nowMonth < 10 ? `0${nowMonth}` : nowMonth
  return { nowMonth, nowYear, nowDay, nowDate, nowHours, nowMinutess }
}
// disabele 
const setDisable = (count, data, maxNum) => {
  if (count > maxNum) { // 
    data.forEach(v => {
      v.disabled = true // ,disabled
    })
  } else {
    data.forEach(v => {
      if (v.childs && v.childs.length) {
        count++
        this.setDisable(count, v.childs, maxNum)
      }
    })
  }
}
const HBTE_API = {
  setDisable,
  getTreeData,
  lastArr,
  addComma,
  nowTime
}
export default HBTE_API



